<!-- eslint-disable max-len -->
<template>
  <main v-if="orderInfoLoadingFailed">
    <h2>:(</h2>
    <span>   10 </span>
  </main>
  <main v-else class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link :to="{name: 'main'}" class="breadcrumbs__link">
            
          </router-link >
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{name: 'cart'}" class="breadcrumbs__link" >
            
          </router-link >
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
          <span> {{ order.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
              .        .
                       .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ order.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{ order.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ order.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ order.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
               {{ order.paymentType}}
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block" v-if="order">
          <ul class="cart__orders">
            <li class="cart__order"
            v-for="item in order.basket.items" :key="item.productOffer.id">
              <h3>{{item.productOffer.title}}</h3>
              <b>{{item.productOffer.price * item.quantity | numberFormat}} </b>
              <span>: {{item.productOffer.id}}</span>
            </li>
          </ul>
          <div class="cart__total">
            <p>{{order.deliveryType.title}}: <b>{{order.deliveryType.price}} </b></p>
            <p>: <b>{{order.basket.items.length | pluralize}}</b>  
              <b>{{order.totalPrice | numberFormat}} </b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import pluralize from '@/helpers/pluralize';

export default {
  name: 'OrderInfoPage',
  filters: {
    numberFormat,
    pluralize,
  },
  computed: {
    order() {
      return this.$store.state.orderInfo;
    },
    orderInfoLoadingFailed() {
      return this.$store.state.orderInfoLoadingFailed;
    },
  },
  created() {
    if (this.$store.state.orderInfo && this.$store.state.orderInfo === this.$route.params.id) {
      return;
    }
    this.$store.dispatch('loadOrderInfo', this.$route.params.id);
  },
};
</script>
